<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
      <div class="right">
        <span class="right-info"> {{tradeAmount}}</span>
        <span class="right-info"> {{tradeAmountNum}}</span>
        <el-button @click="searchOpen" type="primary" size="small"></el-button>
      </div>
    </div>
    <div class="table">
      <el-table
        :data="tableData"
        size="mini"
        show-overflow-tooltip
        header-cell-class-name="table-th"
        style="width: 100%">
        <el-table-column
          v-for="(item,index) in table"
          :key="index"
          :prop="item.prop"
          :label="item.label"
          header-align="center"
          align="center"
        ></el-table-column>
      </el-table>
    </div>
    <el-dialog
      v-show="searchShow"
      title=""
      :visible.sync="searchShow"
      width="30%"
      :before-close="searchClose"
      center
    >
      <div>
        <el-form ref="form" :model="form" :rules="rules" size="small" label-width="105px">
          <el-form-item label="" prop="orderId">
            <el-input v-model="form.orderId"></el-input>
          </el-form-item>
          <el-form-item label="" prop="storeId">
            <el-input v-model="form.storeId"></el-input>
          </el-form-item>
          <el-form-item label="" prop="storeName">
            <el-input v-model="form.storeName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="accountName">
            <el-input v-model="form.accountName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mainAccount">
            <el-input v-model="form.mainAccount"></el-input>
          </el-form-item>
          <el-form-item label="" prop="liquidationStatus">
            <el-checkbox-group v-model="form.liquidationStatus">
              <el-checkbox label="" name="type"></el-checkbox>
              <el-checkbox label="" name="type"></el-checkbox>
              <el-checkbox label="" name="type"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="searchShow = false"> </el-button>
        <el-button size="small" type="primary" @click="submitForm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
  import { formatMoney } from 'static/js/util'
  export default {
    data () {
      return {
        searchShow: false,
        tradeAmount: 100,
        tradeAmountNum: 100,
        tradeStatus: [
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          }
        ],
        liquidationStatus: [
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          }
        ],
        payWay: [
          {
            value: 'alipay',
            label: ''
          },
          {
            value: 'wechat',
            label: ''
          },
          {
            value: 'alipayService',
            label: ''
          },
          {
            value: 'weChatPublic',
            label: ''
          },
          {
            value: 'qq',
            label: 'QQ'
          },
          {
            value: 'gateway',
            label: ''
          },
          {
            value: 'quick',
            label: ''
          },
          {
            value: 'wap',
            label: 'WAP'
          }
        ],
        belongToAgency: [
          {
            value: 'dailishang1',
            label: '1'
          },
          {
            value: 'dailishang2',
            label: '2'
          }
        ],
        table: [
          {
            prop: 'orderId',
            label: ''
          },
          {
            prop: 'storeId',
            label: ''
          },
          {
            prop: 'storeName',
            label: ''
          },
          {
            prop: 'bankId',
            label: ''
          },
          {
            prop: 'bankName',
            label: ''
          },
          {
            prop: 'mainAccount',
            label: ''
          },
          {
            prop: 'accountName',
            label: ''
          },
          {
            prop: 'tradeAmount',
            label: ''
          },
          {
            prop: 'liquidationAmount',
            label: ''
          },
          {
            prop: 'liquidationStatus',
            label: ''
          },
          {
            prop: 'date',
            label: ''
          }
        ],
        tableData: [
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            bankId: '001299',
            bankName: '',
            mainAccount: '6222666633773777',
            accountName: '',
            tradeAmount: '12313.00',
            liquidationAmount: '12313.00',
            liquidationStatus: '',
            date: '2017-11-09'
          },
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            bankId: '001299',
            bankName: '',
            mainAccount: '6222666633773777',
            accountName: '',
            tradeAmount: '12313.00',
            liquidationAmount: '12313.00',
            liquidationStatus: '',
            date: '2017-11-09'
          },
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            bankId: '001299',
