<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
      <div class="right">
        <span class="right-info"> {{tradeAmount}}</span>
        <span class="right-info"> {{tradeAmountNum}}</span>
        <el-button @click="searchOpen" type="primary" size="small"></el-button>
      </div>
    </div>
    <div class="table">
      <el-table
        :data="tableData"
        size="mini"
        show-overflow-tooltip
        header-cell-class-name="table-th"
        style="width: 100%">
        <el-table-column
          v-for="(item,index) in table"
          :key="index"
          :prop="item.prop"
          :label="item.label"
          header-align="center"
          align="center"
        ></el-table-column>
      </el-table>
    </div>
    <el-dialog
      v-show="searchShow"
      title=""
      :visible.sync="searchShow"
      width="30%"
      :before-close="searchClose"
      center
    >
      <div>
        <el-form ref="form" :model="form" :rules="rules" size="small" label-width="105px">
          <el-form-item label="" prop="orderId">
            <el-input v-model="form.orderId"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tradeStatus">
            <el-select v-model="form.tradeStatus" placeholder="">
              <el-option v-for="(item, index) in tradeStatus" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="liquidationStatus">
            <el-select v-model="form.liquidationStatus" placeholder="">
              <el-option v-for="(item, index) in liquidationStatus" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="payWay">
            <el-select v-model="form.payWay" placeholder="">
              <el-option v-for="(item, index) in liquidationStatus" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="belongToAgency">
            <el-select v-model="form.belongToAgency" placeholder="">
              <el-option v-for="(item, index) in belongToAgency" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="daterange">
            <el-date-picker
              v-model="form.daterange"
              type="daterange"
              start-placeholder=""
              end-placeholder=""
              value-format="yyyy-MM-dd"
              default-value="2017-12-01">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="searchShow = false"> </el-button>
        <el-button size="small" type="primary" @click="submitForm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
  import { formatMoney } from 'static/js/util'
  export default {
    data () {
      return {
        searchShow: false,
        tradeAmount: 100,
        tradeAmountNum: 100,
        tradeStatus: [
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          }
        ],
        liquidationStatus: [
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          }
        ],
        payWay: [
          {
            value: 'alipay',
            label: ''
          },
          {
            value: 'wechat',
            label: ''
          },
          {
            value: 'alipayService',
            label: ''
          },
          {
            value: 'weChatPublic',
            label: ''
          },
          {
            value: 'qq',
            label: 'QQ'
          },
          {
            value: 'gateway',
            label: ''
          },
          {
            value: 'quick',
            label: ''
          },
          {
            value: 'wap',
            label: 'WAP'
          }
        ],
        belongToAgency: [
          {
            value: 'dailishang1',
            label: '1'
          },
          {
            value: 'dailishang2',
            label: '2'
          }
        ],
        table: [
          {
            prop: 'orderId',
            label: ''
          },
          {
            prop: 'storeId',
            label: ''
          },
          {
            prop: 'storeName',
            label: ''
          },
          {
            prop: 'agencyId',
            label: 'ID'
          },
          {
            prop: 'tradeMoney',
            label: ''
          },
          {
            prop: 'tradeStatus',
            label: ''
          },
          {
            prop: 'tradeWay',
            label: ''
          },
          {
            prop: 'orderStatus',
            label: ''
          },
          {
            prop: 'date',
            label: ''
          }
        ],
        tableData: [
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            agencyId: 'agency123123',
            tradeMoney: '12313.00',
            tradeStatus: '',
            tradeWay: '',
            orderStatus: '',
            date: '2017-11-09'
          },
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            agencyId: 'agency123123',
            tradeMoney: '12313.00',
            tradeStatus: '',
            tradeWay: '',
            orderStatus: '',
            date: '2017-11-09'
          },
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            agencyId: 'agency123123',
            tradeMoney: '12313.00',
            tradeStatus: '',
            tradeWay: '',
            orderStatus: '',
            date: '2017-11-09'
          },
          {
            orderId: 'order123412',
            storeId: 'store12313',
            storeName: '',
            agencyId: 'agency123123',
            tradeMoney: '12313.00',
            tradeStatus: '',
            tradeWay: '',
            orderStatus: '',
            date: '2017-11-09'
          }
        ],
        form: {
          orderId: '',
          tradeStatus: '',
          liquidationStatus: '',
          payWay: '',
          belongToAgency: '',
          daterange: ''
        },
        rules: {
          orderId: [
//            {required: true, message: '', trigger: 'blur'},
            { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ]
        }
      }
    },
    created () {
      this.tradeAmount = formatMoney(100)
      this.tradeAmountNum = 100
    },
    methods: {
      searchOpen () {
        this.searchShow = true
      },
      searchClose () {
        this.searchShow = false
        this.$refs['form'].resetFields()
      },
      submitForm () {
        this.$refs['form'].validate((valid) => {
          if (valid) {
            const {orderId, tradeStatus, liquidationStatus, payWay, belongToAgency, daterange} = this.form
            console.log('submit!', this.form, {orderId, tradeStatus, liquidationStatus, payWay, belongToAgency, daterange})
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }
</script>



